* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  color: #808080;
  font-family: "myriad-pro", Helvetica, Arial, sans-serif;
}
.Nav_top {
  width: 90%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}
.C_logo {
  padding-top: 40px;
}
.Need_hlp {
  display: flex;
  justify-content: flex-end;
}
.Need_hlp a {
  text-decoration: none;
  color: #008bdf;
}
.Need_hlp p {
  width: 280px;
  background-color: rgba(128, 128, 128, 0.527);
  height: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0 0 10px 10px;
}
.Menu {
  display: none;
}
.Company_list {
  width: 100%;
  height: 100px;
  background-color: #60b9ee;
  display: flex;
  padding: 0 4rem;
  align-items: center;
  justify-content: space-between;
}
.Company_location {
  color: #fff;
}

.Company_list .list ol {
  display: flex;
}
.Company_list .list ol li {
  display: flex;
  align-items: center;
  font-size: 24px;
  cursor: pointer;
  color: #fff;
}
.list .fa-long-arrow-alt-right {
  padding: 4px 4px 0 4px;
  font-size: 25px;
  cursor: default;
}
.About_Apply {
  background-color: #ebebeb;
  display: flex;
  justify-content: center;
  padding: 1.3rem 0;
  width: 100%;
}
.About_dscrptn_box {
  background-color: #fff;
  width: 23%;
  height: 550px;
  border: 1px solid #e0e0e0;
  border-radius: 5px;
  margin: 15px;
}
.ABout_para_btn h3 {
  padding: 18px 23px;
}
.ABout_para_btn p {
  margin: 0 0 10px 0;
  padding: 0 23px 18px 23px;
}
.Apply_box {
  background-color: #fff;
  width: 67%;
  height: 550px;
  border-radius: 5px;
  border: 1px solid #e0e0e0;

  margin: 15px 30px 15px 15px;
}
.ABout_para_btn {
  margin-bottom: 15px;
}
.side_para {
  border-top: 1px solid #e0e0e0;
  font-size: 16px;
}
.side_VJDbtn,
.Start_Apply_btn {
  text-align: center;
}
.side_VJDbtn button {
  border-radius: 20px;
  border: 3px solid #80c844;
  background-color: #fff;
  color: #80c844;
  font-weight: 600;
  padding: 7px 10px 7px;
  cursor: pointer;
}
.side_VJDbtn button:hover {
  background-color: #80c844;
  color: #fff;
}
.side_para p {
  margin: 20px 0 20px 0;
  font-size: 16px;

  padding: 0 23px 28px 23px;
}
.Upload_resume {
  margin-top: 30px;
  background-color: #c1e1f1a6;
}
.resume_h3 {
  text-align: center;
  padding: 20px 0;
  font-size: 20px;
}
.resume_supports {
  width: 200px;
  text-align: center;
  margin: 40px auto;
}
.Upload_new_resume_btn {
  margin-top: 10px;
  padding-bottom: 20px;
  display: flex;
  justify-content: center;
}
.Upload_new_resume_btn button {
  border: none;
  width: auto;
  background-color: #80c844;
  color: white;
  font-weight: 600;
  padding: 7px 10px 7px;
  cursor: pointer;
  border-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.Next_step_btn {
  display: flex;
  margin: 20px 40px;
  justify-content: flex-end;
}
.Next_step_btn button {
  border: 1px solid #e0e0e0;
  display: flex;
  align-items: center;
  cursor: pointer;
  justify-content: space-between;
  color: #808080;
  border-radius: 20px;
  width: 130px;
  padding: 5px;
  font-size: 21px;
}

