    width: 70px;
    cursor: pointer;
    border-radius: 20px;
    background-color: #fff;
    padding: 5px;
  }
  .Menu button span {
    font-weight: bold;
  }

  .C_logo {
    padding-top: 10px;
  }
  .C_logo img {
    height: 30px;
    width: 140px;
  }

  .Company_list {
    width: 100%;
    height: 70px;
    background-color: #60b9ee;
    display: flex;
    padding: 0;
    align-items: center;
    justify-content: center;
  }
  .Company_list .Company_location {
    display: none;
