    display: flex;
    align-items: center;
    justify-content: center;
  }
  .Company_list .list ol li {
    font-size: 17px;
  }
  .list .fa-long-arrow-alt-right {
    padding: 5px 4px 0 4px;
    font-size: 18px;
  }
  .About_Apply,
  .Apply_box {
    background-color: #ebebeb;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0.5rem 0;
    width: 100%;
    height: auto;
    margin: 0;
  }
  .About_dscrptn_box {
    width: 100%;
    height: auto;
    border-radius: 5px;
    margin: 0;
  }
  .ABout_para_btn h3 {
    padding: 10px 20px;
    text-align: center;
  }
  .ABout_para_btn p {
    margin: 0 0 10px 0;
    padding: 0 10px 15px 10px;
    text-align: center;
  }
  .side_para p {
    margin: 15px 0 15px 0;
    padding: 0 20px 15px 20px;
    text-align: center;
  }
  .Upload_resume {
    margin-top: 15px;
  }
  .resume_h3 {
    padding: 15px 0;
    font-size: 19px;
  }
  .resume_supports {
    width: 250px;
    text-align: center;
    margin: 20px auto;
  }
  .Next_step_btn {
    margin: 12px 17px;
  }
  .Next_step_btn button {
    width: 85px;
    padding: 4px;
    font-size: 15px;
  }
}

